.container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    gap: 20px;
}

.pd_box {
    width: 200px;
    height: 200px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.pd_img {
    width: 100%;
}

.pd_img img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.pd_info {
    text-align: center;
}
